.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

html.home .banner {
  height: 100vh;
  background: linear-gradient(rgba(115, 0, 123, 0.85) 0%, rgba(0, 124, 156, 0.93) 100%), url("../../assets/img/home_banner_bg.png"); }

html.home .personal-container {
  height: 100%;
  padding: 2rem; }
  html.home .personal-container .personal {
    width: 413px;
    height: 100%;
    background: #ffffff;
    border-radius: 25px;
    padding: 50px 30px; }
    html.home .personal-container .personal img {
      width: 165px;
      height: 165px;
      margin-bottom: 25px; }
    html.home .personal-container .personal h4 {
      letter-spacing: 0.02em;
      line-height: 23px;
      text-align: center;
      color: #717171;
      text-transform: uppercase;
      margin-bottom: 15px; }
      html.home .personal-container .personal h4 span {
        font-weight: 300; }
    html.home .personal-container .personal h5 {
      font-family: Roboto;
      font-weight: 500;
      font-size: 13px;
      letter-spacing: 0.02em;
      line-height: 13px;
      text-align: center;
      color: #02a47f;
      text-transform: uppercase;
      margin-bottom: 30px; }
    html.home .personal-container .personal p {
      font-family: Roboto;
      font-weight: normal;
      font-style: italic;
      font-size: 17px;
      letter-spacing: 0.02em;
      line-height: 22px;
      text-align: center;
      color: #7b7b7b; }

